<template>
    <!-- <button @click="getData">get data</button> -->
    <div class="bg-filter"></div>
    <div id="header">
      <div class="container">
        <div class="header-info">Welcome!</div>
      </div>
    </div>
    <div id="content" @mousewheel="getMousePosition">
      <div class="container">
        <div class="repos">
          <div class="row">
            <div class="repo-item col-12 col-sm-6 col-md-4 col-lg-3" v-for="repo in githubRepoData" :key="repo.id">
              <div class="pd-box">
                <div class="repo-info">
                  <div class="title"><span>repo name :</span><br>{{repo.name}}</div>
                  <div class="desc"><span>description :</span><br>{{repo.description}}</div>
                  <div class="url"><a :href="repo.homepage">go to page</a></div>
                </div>
              </div>
            </div>
             <div class="repo-item col-12 col-sm-6 col-md-4 col-lg-3" v-for="(loremRepo, index) in loremData" :key="index">
              <div class="pd-box">
                <div class="repo-info">
                  <div class="title"><span>repo name :</span><br>{{loremRepo.name}}</div>
                  <div class="desc"><span>description :</span><br>{{loremRepo.desc}}</div>
                  <div class="url"><a href="#">go to page</a></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import axios from 'axios'


export default {
  name: 'App',
  components: {
  },
  data(){
    return{
      githubRepoData: null,
      loremData: [
        {
          name: "Lorem, ipsum dolor.-1",
          desc: "Lorem ipsum dolor sit amet consectetur.",
        },
        {
          name: "Lorem, ipsum dolor.-2",
          desc: "Lorem ipsum dolor sit amet consectetur.",
        },
        {
          name: "Lorem, ipsum dolor.-3",
          desc: "Lorem ipsum dolor sit amet consectetur.",
        },
        {
          name: "Lorem, ipsum dolor.-4",
          desc: "Lorem ipsum dolor sit amet consectetur.",
        },
      ],
      isBottom: false,
      scrollTop: null,
    };
  },
  methods: {
    getData(){
      // axios
      // .get('https://api.github.com/search/repositories?q=user:charlesx19')
      // .then(response => (this.githubRepoData = response.items))
      // .catch(function (error) { // 请求失败处理
      //   console.log(error);
      // });
      console.log(this.githubRepoData)
    },
    getMousePosition() {
      let scrolled = document.documentElement.scrollTop / (document.documentElement.scrollHeight - document.documentElement.clientHeight);
      this.scrollTop = document.documentElement.scrollTop;

      if (scrolled > 0.99) {
        this.updateLoremData();
      } 
      // let scrollTop = document.documentElement.scrollTop;
    },
    updateLoremData(){

      let newData;
      for (var i=0; i<this.loremData.length+1; i++) {
        newData = {
          name: "Lorem, ipsum dolor.-"+(i+1),
          desc: "Lorem ipsum dolor sit amet consectetur.",
        }
      }
      this.loremData.push(newData);
    },
  },
  watch: {
    scrolled(){
      this.getMousePosition();
    }
  },
  mounted(){
    axios
      .get('https://api.github.com/search/repositories?q=user:charlesx19')
      .then(response => (this.githubRepoData = response.data.items))
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
  },
}
</script>
